@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
}

.activities,
.heartrate,
.nutrition,
.sleep,
.wellness {
  display: flex;
  height: 90vh;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
}
.menu-bars {
	margin-left: 2rem;
	font-size: 2rem;
	background: none;
}
  
.nav-menu {
	background-color: #060b26;
	width: 250px;
	height: 100vh;
	display: flex;
	justify-content: center;
	position: fixed;
	top: 0;
	left: -100%;
	transition: 850ms;
	left: 0;
}
  
.nav-text {
	display: flex;
	justify-content: start;
	align-items: center;
	padding: 8px 0px 8px 16px;
	list-style: none;
	height: 60px;
}
  
.nav-text a {
	text-decoration: none;
	color: #f5f5f5;
	font-size: 18px;
	width: 95%;
	height: 100%;
	display: flex;
	align-items: center;
	padding: 0 16px;
	border-radius: 4px;
}
  
.nav-text a:hover {
	background-color: #1a83ff;
}
  
.nav-menu-items {
	width: 100%;
}
  
span {
	margin-left: 16px;
}

.placeholder-logo{
	margin-top: 20px;
	margin-left: 65px;
	margin-bottom: 25px;
}
.featured{
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: space-between;
}

.featuredItem{
    flex: 1 1;
    margin: 100px 30px;
    padding: 50px;
    width: 200px;
    border-radius: 10px;
    cursor: pointer;
    box-shadow: 0px 0px 15px -10px rgba(0,0,0,0.75);
    
}

.featuredTitle{
    font-size: 25px;
}

.featuredExercisesTimeContainer,
.featuredSleepTimeContainer,
.featuredStepsContainer{
    padding: 10px, 0, 10px, 0;
    display: flex;
    align-items: center;
}

.featuredExercisesTime,
.featuredSleepTime,
.featuredSteps{
    font-size: 20px;
    font-weight: 600;
    padding-top: 10px;
    padding-bottom: 10px;
}

.featuredIcon{
    font-size: 14px;
    margin-left: 5px;
    color: green;
}

.featuredSub{
    font-size: 15px;
    color: gray;
}
.home{
    padding-left: 250px;
}
