.menu-bars {
	margin-left: 2rem;
	font-size: 2rem;
	background: none;
}
  
.nav-menu {
	background-color: #060b26;
	width: 250px;
	height: 100vh;
	display: flex;
	justify-content: center;
	position: fixed;
	top: 0;
	left: -100%;
	transition: 850ms;
	left: 0;
}
  
.nav-text {
	display: flex;
	justify-content: start;
	align-items: center;
	padding: 8px 0px 8px 16px;
	list-style: none;
	height: 60px;
}
  
.nav-text a {
	text-decoration: none;
	color: #f5f5f5;
	font-size: 18px;
	width: 95%;
	height: 100%;
	display: flex;
	align-items: center;
	padding: 0 16px;
	border-radius: 4px;
}
  
.nav-text a:hover {
	background-color: #1a83ff;
}
  
.nav-menu-items {
	width: 100%;
}
  
span {
	margin-left: 16px;
}

.placeholder-logo{
	margin-top: 20px;
	margin-left: 65px;
	margin-bottom: 25px;
}